import React, { useState } from "react";
import Modal from "react-modal";
import { toast } from "react-toastify";
import { directusLoaderLink } from "~/utils/constants";
import ConfirmationModal from "./confirmationModal";
import { addDays, format } from "date-fns";

function SpaActivityModal({
  content,
  IsModalOpen,
  date,
  closeHandle,
  setRefresh,
  refresh,
  locationId,
  isAdmin,
}) {
  const [modalIsOpen, setIsOpen] = useState(
    IsModalOpen === true ? true : false
  );
  const DateFormat = "yyyy-MM-dd";
  const [title, setTitle] = useState(
    !!content?.treatment ? content?.treatment : ""
  );
  const [description, setDescription] = useState(
    !!content?.description ? content?.description : ""
  );
  const [price, setPrice] = useState(!!content?.price ? content?.price : "");
  const [time, setTime] = useState(!!content?.time ? content?.time : "");
  const [preventDouble, setPreventDouble] = useState(false);
  const [modalLoader, setModalLoader] = useState(false);
  const [confirmModalIsOpen, setConfirmIsOpen] = useState(false);

  // const customStyles = {
  //   content: {
  //     top: "50%",
  //     left: "50%",
  //     right: "auto",
  //     bottom: "auto",
  //     marginRight: "-50%",
  //     transform: "translate(-50%, -50%)",
  //     minWidth: "450px",
  //     zIndex: 20,
  //     backgroundColor: "#E7ECE6",
  //   },
  //   overlay: {
  //     zIndex: 20,
  //     backgroundColor: "#1d1d1b66 !important",
  //   },
  // };
  React.useEffect(() => {
    if (title?.length > 100) {
      setTitle(title?.substring(0, 100));
    }
    if (description?.length > 100) {
      setDescription(description?.substring(0, 100));
    }
  }, [title, description]);
  const cancelHandle = () => {
    setIsOpen(false);
    closeHandle();
  };
  const createHandle = async (e) => {
    setPreventDouble(true);
    const body = {
      activity_type: "activities",
      treatment: title,
      date: format(date, DateFormat),
      time: time,
      location_id: locationId,
      price: !!price ? price : null,
      description: !!description ? description?.trim() : null,
    };
    if (!!title) {
      if (!!time) {
        if (content?.id) {
          // update handle
          await directusLoaderLink
            .items("spa")
            .updateOne(content?.id, body)
            .then((res) => {
              toast.success("Updated successfully");
              cancelHandle();
              setRefresh(!refresh);
            })
            .catch((err) => {
              console.log(err);
              toast.error(err);
            });
        } else {
          // create handle
          await directusLoaderLink
            .items("spa")
            .createOne(body)
            .then((res) => {
              toast.success(" Created successfully");
              cancelHandle();
              setRefresh(!refresh);
            })
            .catch((err) => {
              console.log(err);
              toast.error(err);
            });
        }
        localStorage.setItem(
          "SpaWacthedWeek",
          new Date(date)?.getDay() == 0
            ? format(addDays(new Date(date), -2), "yyyy-MM-dd")
            : date
        );
      } else {
        toast.error("Time is required");
      }
    } else {
      toast.error("Title is required");
    }
    setPreventDouble(false);
  };
  const deleteHandle = async () => {
    if (content?.id) {
      setModalLoader(true);
      await directusLoaderLink
        .items("spa")
        .deleteOne(content?.id)
        .then((res) => {
          toast.success("Data deleted successfully");
        })
        .catch((err) => {
          console.log(err);
        });
      setModalLoader(false);
      setRefresh(!refresh);
      cancelHandle();
      location.reload();
    }
  };
  const confirmCancelHandle = () => {
    setConfirmIsOpen(false);
  };
  return (
    <Modal
      isOpen={modalIsOpen}
      // onRequestClose={cancelHandle}
      contentLabel="Logout Modal"
      ariaHideApp={false}
      className="spaModal"
      overlayClassName="spaModal_overlay"
    >
      <div
        className={`
          flex flex-col p-4 font-normal min-w-[300px] min-h-[300px] bg-[#EFF0EF] border-1 left-5 z-10 gap-4 text-sm top-12`}
      >
        <div className="close-div">
          <button className="close-act" onClick={cancelHandle}>
            <i className="ri-close-line text-primary"></i>
          </button>
        </div>
        <div className="col-span-6">
          <label
            htmlFor="title"
            className="block text-sm font-medium text-gray-700 uppercase"
          >
            Treatement
          </label>
          <div className="relative spa-title">
            <textarea
              type="text"
              name="title"
              id="title"
              placeholder="Enter the title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              className="mt-1 block w-full rounded-md border border-primary py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
            />
            <span className="text-[10px] absolute bottom-0 right-2">
              {title?.length}/100
            </span>
          </div>
        </div>
        <div className="col-span-6">
          <label
            htmlFor="description"
            className="block text-sm font-medium text-gray-700 uppercase"
          >
            Description
          </label>
          <div className="relative spa-title">
            <textarea
              type="text"
              name="description"
              id="description"
              placeholder="Enter the description"
              value={description || ""}
              onChange={(e) => setDescription(e.target.value)}
              className="resize-none min-h-[80px] mt-1 block w-full rounded-md border border-primary py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
            />
            <span className="text-[10px] absolute bottom-0 right-2">
              {description?.length}/100
            </span>
          </div>
        </div>
        <div className="flex flex-row gap-6">
          <div className="w-1/2 price">
            <label
              htmlFor="price"
              className="block text-sm font-medium text-gray-700 uppercase"
            >
              Price
            </label>
            <div className="relative mt-1">
              <input
                type="number"
                name="price"
                id="price"
                placeholder="Enter the price"
                value={price}
                onChange={(e) => setPrice(e.target.value)}
                className=" block w-full rounded-md border border-primary py-2 px-3 pr-[30px] shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
              />
              <span className=" absolute right-3  bottom-[25%] text-center">
                kr
              </span>
            </div>
          </div>
          <div className="w-1/2 time">
            <label
              htmlFor="price"
              className="block text-sm font-medium text-gray-700 uppercase"
            >
              Time / Slot
            </label>
            <input
              type="time"
              name="Timing"
              id="Timing"
              placeholder="00:00"
              value={time?.slice(0, 5) || "00:00"}
              onChange={(e) => setTime(e.target.value)}
              className="mt-1 block w-full rounded-md border border-primary py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
            />
          </div>
        </div>
        <div
          className={`flex ${
            content?.id && isAdmin ? "justify-between" : "justify-end"
          }`}
        >
          {content?.id && isAdmin && (
            <button
              className="py-1 px-2 border text-white border-red-700 bg-red-700 text-sm font-medium rounded-[5px]"
              onClick={() => setConfirmIsOpen(true)}
              title="delete"
            >
              <i className="ri-delete-bin-line"></i>
            </button>
          )}
          <button
            className="py-1 px-2 border text-white border-primary bg-primary text-sm font-medium rounded-[5px]"
            onClick={() => createHandle()}
            disabled={preventDouble}
            title="save"
          >
            {preventDouble ? "Saving..." : "Save"}
          </button>
        </div>
      </div>
      {confirmModalIsOpen && (
        <ConfirmationModal
          modalIsOpen={confirmModalIsOpen}
          setIsOpen={setConfirmIsOpen}
          content="Are you sure to delete data?"
          callFunction={deleteHandle}
          cancelHandle={confirmCancelHandle}
          Loader={modalLoader}
        />
      )}
    </Modal>
  );
}

export default SpaActivityModal;
