import React, { useState } from "react";
import Modal from "react-modal";
import { toast } from "react-toastify";
import { directusLoaderLink } from "~/utils/constants";
import ConfirmationModal from "./confirmationModal";
import { addDays, format } from "date-fns";

function SpaThreeColModal({
  content,
  IsModalOpen,
  ActivityName,
  date,
  closeHandle,
  setRefresh,
  refresh,
  locationId,
  isAdmin,
}) {
  const DateFormat = "yyyy-MM-dd";
  const [modalIsOpen, setIsOpen] = useState(
    IsModalOpen === true ? true : false
  );
  const [confirmModalIsOpen, setConfirmIsOpen] = useState(false);
  const [title, setTitle] = useState(
    !!content?.treatment ? content?.treatment : ""
  );
  const [description, setDescription] = useState(
    !!content?.description ? content?.description : ""
  );
  const [price, setPrice] = useState(!!content?.price ? content?.price : "");
  const [price1, setPrice1] = useState(
    !!content?.price_1 ? content?.price_1 : ""
  );
  const [price2, setPrice2] = useState(
    !!content?.price_2 ? content?.price_2 : ""
  );
  const [title1, setTitle1] = useState(
    !!content?.treatment_1 ? content?.treatment_1 : ""
  );
  const [description1, setDescription1] = useState(
    !!content?.description_1 ? content?.description_1 : ""
  );
  const [title2, setTitle2] = useState(
    !!content?.treatment_2 ? content?.treatment_2 : ""
  );
  const [description2, setDescription2] = useState(
    !!content?.description_2 ? content?.description_2 : ""
  );
  const [preventDouble, setPreventDouble] = useState(false);
  const [modalLoader, setModalLoader] = useState(false);

  const cancelHandle = () => {
    setIsOpen(false);
    closeHandle();
  };
  React.useEffect(() => {
    if (title?.length > 100) {
      setTitle(title?.substring(0, 100));
    }
    if (description?.length > 100) {
      setDescription(description?.substring(0, 100));
    }
    if (title1?.length > 100) {
      setTitle1(title1?.substring(0, 100));
    }
    if (description1?.length > 100) {
      setDescription1(description1?.substring(0, 100));
    }
    if (title2?.length > 100) {
      setTitle2(title2?.substring(0, 100));
    }
    if (description2?.length > 100) {
      setDescription2(description2?.substring(0, 100));
    }
  }, [title, description, title1, description1, title2, description2]);
  const BodyContent = ({
    titleName,
    setTitleName,
    descriptionName,
    setDescriptionName,
    border,
    priceName,
    setPriceName,
  }) => {
    return (
      <div
        className={`
        flex py-6 px-2 font-normal min-w-[750px] min-h-[150px] border-1 line-normal left-5 z-10 gap-[15px] text-sm top-12 ${
          border ? " border-b-2 border-gray-300" : ""
        }`}
      >
        <div className="title-price grid gap-y-[15px] w-1/2">
          <div className="col-span-6">
            <label
              htmlFor="title"
              className="block text-sm font-medium text-gray-700 uppercase"
            >
              title
            </label>
            <div className="relative spa-title">
              <textarea
                type="text"
                name="title"
                id="title"
                placeholder="Enter the title"
                value={titleName}
                onChange={(e) => setTitleName(e.target.value)}
                className="resize-none min-h-[80px] mt-1 block w-full rounded-md border border-primary py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
              />
              <span className="text-[10px] absolute bottom-0 right-2">
                {titleName?.length}/100
              </span>
            </div>
          </div>
          <div className="col-span-6 ">
            <label
              htmlFor="price"
              className="block text-sm font-medium text-gray-700 uppercase"
            >
              Price
            </label>
            <div className="relative mt-1">
              <input
                type="number"
                name="price"
                id="price"
                placeholder="Enter the price"
                value={priceName}
                onChange={(e) => setPriceName(e.target.value)}
                className=" block w-full rounded-md border border-primary py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
              />
              <span className=" absolute right-6  bottom-[25%] text-center">
                kr
              </span>
            </div>
          </div>
        </div>
        <div className="desc w-1/2">
          <div className="col-span-6">
            <label
              htmlFor="description"
              className="block text-sm font-medium text-gray-700 uppercase"
            >
              Description
            </label>
            <div className="relative spa-title">
              <textarea
                type="text"
                name="description"
                id="description"
                placeholder="Enter the description"
                value={descriptionName || ""}
                onChange={(e) => setDescriptionName(e.target.value)}
                className="resize-none min-h-[80px] mt-1 block w-full rounded-md border border-primary py-2 px-3 shadow-sm placeholder:text-gray-400 placeholder:!text-sm placeholder:font-medium focus:border-primary  focus:outline-none focus:ring-primary sm:text-sm"
              />
              <span className="text-[10px] absolute bottom-0 right-2">
                {descriptionName?.length}/100
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  };
  const createHandle = async (e) => {
    setPreventDouble(true);
    const body = {
      activity_type: ActivityName,
      date: format(date, DateFormat),
      location_id: locationId,
      treatment: title,
      price: !!price ? price : null,
      description: !!description ? description?.trim() : null,
      treatment_1: title1,
      price_1: !!price1 ? price1 : null,
      description_1: !!description1 ? description1?.trim() : null,
      treatment_2: title2,
      price_2: !!price2 ? price2 : null,
      description_2: !!description2 ? description2?.trim() : null,
    };
    if (!!title) {
      const section2title = !!description1
        ? description1?.trim()?.length > 0
        : false || !!price1
        ? price1
        : false;
      if (section2title ? !!title1 : true) {
        const section3title = !!description2
          ? description2?.trim()?.length > 0
          : false || !!price2
          ? price2
          : false;
        if (section3title ? !!title2 : true) {
          if (content?.id) {
            // update handle
            await directusLoaderLink
              .items("spa")
              .updateOne(content?.id, body)
              .then((res) => {
                toast.success("Updated successfully");
                cancelHandle();
                setRefresh(!refresh);
              })
              .catch((err) => {
                console.log(err);
                toast.error(err);
              });
          } else {
            // create handle
            await directusLoaderLink
              .items("spa")
              .createOne(body)
              .then((res) => {
                toast.success(" Created successfully");
                cancelHandle();
                setRefresh(!refresh);
              })
              .catch((err) => {
                console.log(err);
                toast.error(err);
              });
          }
          localStorage.setItem(
            "SpaWacthedWeek",
            new Date(date)?.getDay() == 0
              ? format(addDays(new Date(date), -2), "yyyy-MM-dd")
              : date
          );
        } else {
          toast.error("Section 3 Title is required");
        }
      } else {
        toast.error("Section 2 Title is required");
      }
    } else {
      toast.error("Section 1 Title is required");
    }
    setPreventDouble(false);
  };
  const deleteHandle = async () => {
    if (content?.id) {
      setModalLoader(true);
      await directusLoaderLink
        .items("spa")
        .deleteOne(content?.id)
        .then((res) => {
          toast.success("Data deleted successfully");
        })
        .catch((err) => {
          console.log(err);
        });

      setModalLoader(false);
      setRefresh(!refresh);
      cancelHandle();
      location.reload();
    }
  };
  const confirmCancelHandle = () => {
    setConfirmIsOpen(false);
  };
  return (
    <Modal
      isOpen={modalIsOpen}
      // onRequestClose={cancelHandle}
      contentLabel="Logout Modal"
      ariaHideApp={false}
      className="spaModal"
      overlayClassName="spaModal_overlay"
    >
      <div className="close-div">
        <button className="close-act" onClick={cancelHandle}>
          <i className="ri-close-line text-primary"></i>
        </button>
      </div>
      <div className="spa-three-row">
        <div className="flex justify-center uppercase text-primary font-bold">
          {ActivityName}
        </div>
        {BodyContent({
          titleName: title,
          setTitleName: setTitle,
          descriptionName: description,
          setDescriptionName: setDescription,
          priceName: price,
          setPriceName: setPrice,
          border: true,
        })}
        {BodyContent({
          titleName: title1,
          setTitleName: setTitle1,
          descriptionName: description1,
          setDescriptionName: setDescription1,
          priceName: price1,
          setPriceName: setPrice1,
          border: true,
        })}
        {BodyContent({
          titleName: title2,
          setTitleName: setTitle2,
          descriptionName: description2,
          setDescriptionName: setDescription2,
          priceName: price2,
          setPriceName: setPrice2,
          border: false,
        })}
        <div
          className={`flex ${
            content?.id && isAdmin ? "justify-between" : "justify-end"
          }`}
        >
          {content?.id && isAdmin && (
            <button
              className="py-1 px-2 border text-white border-red-700 bg-red-700 text-sm font-medium rounded-[5px]"
              onClick={() => setConfirmIsOpen(true)}
              title="delete"
            >
              <i className="ri-delete-bin-line"></i>
            </button>
          )}
          <button
            className="py-1 px-2 border text-white border-primary bg-primary text-sm font-medium rounded-[5px]"
            onClick={() => createHandle()}
            disabled={preventDouble}
            title="save"
          >
            {preventDouble ? "Saving..." : "Save"}
          </button>
        </div>
      </div>
      {confirmModalIsOpen && (
        <ConfirmationModal
          modalIsOpen={confirmModalIsOpen}
          setIsOpen={setConfirmIsOpen}
          content="Are you sure to delete data?"
          callFunction={deleteHandle}
          cancelHandle={confirmCancelHandle}
          Loader={modalLoader}
        />
      )}
    </Modal>
  );
}

export default SpaThreeColModal;
