export const action = async ({ request }) => {
  await authLogout(request);
};

export const loader = async ({ request, location }) => {
  await isLoggedIn(request);
  const session = await sessionStorage.getSession(
    request.headers.get("Cookie")
  );
  var sessionkey = session.get("sessionKey");
  const directus = await getDirectusClient(sessionkey);
  const current = await directus.users.me.read({
    fields: ["*", "selected_locations.*.*.*", "allowed_locations.*.*.*"],
  });
  const start = format(
    startOfWeek(new Date(), { weekStartsOn: 1 }),
    "yyyy-MM-dd"
  );
  const end = format(
    startOfWeek(addDays(new Date(), 7), { weekStartsOn: 0 }),
    "yyyy-MM-dd"
  );
  const currentLocationId = current.selected_locations?.id
    ? current.selected_locations?.id
    : current?.allowed_locations?.[0]?.Locations_id?.id;
  const spaDetails = await directus
    .items("spa")
    .readByQuery({
      fields: ["*.*.*"],
      filter: {
        date: { _between: [start, end] },
        location_id: { _eq: currentLocationId },
      },
      sort: ["time", "id"],
    })
    .then((e) => e?.data);

  return json({
    locationId: current.selected_locations?.id
      ? current.selected_locations?.id
      : current?.allowed_locations?.[0]?.Locations_id?.id,
    currentUser: current,
    year: getYear(new Date()),
    spaDetails,
  });
};

